const BaseUrl='https://meituan.thexxdd.cn/api'

export function getOptimization(){
	return new Promise((resolve,reject)=>{
		uni.request({
			url:`${BaseUrl}/forshop/getprefer`,
			method:'GET',
			success: (res) => {
				resolve(res)
			},
			fail: (err) => {
				reject(err)
			}
		})
		
	})
}
export function getNearbyshops(){
	return new Promise((resolve,reject)=>{
		uni.request({
			url:`${BaseUrl}/forshop/wxshop`,
			method:'GET',
			success: (res) => {
				resolve(res)
			},
			fail: (err) => {
				reject(err)
			}
		})
		
	})
}
