const BaseUrl='https://meituan.thexxdd.cn/api'

export function getShopInfo(data){
	return new Promise((resolve,reject)=>{
		uni.request({
			url:`${BaseUrl}/forshop/shop`,
			method:'POST',
			data:data,
			success: (res) => {
				resolve(res)
			},
			fail: (err) => {
				reject(err)
			}
		})
		
	})
}
